.standings {
  padding: 15px 20px;
}
.standings__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}
.standings__head-title {
  font-weight: 700;
  font-size: 16px;
  color: var(--primary-font);
}
.standings__head button {
  width: 148px;
}
.standings__caption {
  display: grid;
  grid-template-columns: 26px minmax(100px, 1fr) repeat(2, 26px) 120px 38px;
  grid-column-gap: 15px;
}
.standings__caption > div {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
}
.standings__caption > div:nth-child(2) span {
  width: auto;
  padding: 0 10px;
}
.standings__caption span {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  padding: 0 5px;
  font-weight: 400;
  font-size: 12px;
  border-radius: 5px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.standings__caption span:hover {
  background: var(--primary);
}